@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.row_country {
    align-items: center;
    justify-content: center;
}
*{box-sizing:border-box;margin:0;padding:0}body{margin:.4rem auto;max-width:1296px;font-family:"Poppins",sans-serif;color:#282c34;background-color:#f7f7f7}li{list-style-type:none}a{text-decoration:none;color:#282c34;cursor:pointer}a:visited{color:#282c34}.navigation{margin:26px 0;display:flex}.navigation a{margin:10px;padding:10px;border-radius:4px;position:relative}.navigation .nav-active::after{content:"";width:85%;height:5px;background:#61dafb;position:absolute;bottom:1px;left:50%;transform:translateX(-50%)}.btn{margin:auto;display:table}
